<template>
    <div class="overflow-hidden block">
      <div class="my-3"> 
        <h2 class="px-3 py-2 font-bold bg-gray-800 border-t border-r border-l border-gray-700"> 누적스탯 </h2>
      <grid v-if="gridProps" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options" :language="'ko'"  />
      </div>
      <v-chart ref="eschart" :options="option" :theme="polarTheme" autoresize style="width:100%"/>
    </div>
</template>

<script>
import 'tui-grid/dist/tui-grid.css';
import { Grid } from '@toast-ui/vue-grid';
import ECharts from 'vue-echarts';
import 'echarts/lib/chart/line';
import 'echarts/lib/component/polar';
//import { Dark } from 'echarts/theme/dark.js';
import 'echarts/theme/dark';

export default {
  name: 'pstat',
  components: {
    'grid': Grid,
    'v-chart': ECharts
  },
  props: ['season', 'name', 'pcode',],
  data() {
    let data = []

    for (let i = 0; i <= 360; i++) {
        let t = i / 180 * Math.PI
        let r = Math.sin(2 * t) * Math.cos(2 * t)
        data.push([r, i])
    }
    return {
      //gridProps: null,
      polarTheme : 'dark',
      tuiGrid : null,
      MutilPerson : false,
      url : false,
      option : {
        title: {
            text: '折线图堆叠'
        },
        tooltip: {
            trigger: 'axis'
        },
        legend: {
            data: ['邮件营销', '联盟广告', '视频广告', '直接访问', '搜索引擎']
        },
        grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
        },
        toolbox: {
            feature: {
                saveAsImage: {}
            }
        },
        xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
        },
        yAxis: {
            type: 'value'
        },
        series: [
          {
              name: '邮件营销',
              type: 'line',
              stack: '总量',
              data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
              name: '联盟广告',
              type: 'line',
              stack: '总量',
              data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
              name: '视频广告',
              type: 'line',
              stack: '总量',
              data: [150, 232, 201, 154, 190, 330, 410]
          },
          {
              name: '直接访问',
              type: 'line',
              stack: '总量',
              data: [320, 332, 301, 334, 390, 330, 320]
          },
          {
              name: '搜索引擎',
              type: 'line',
              stack: '总量',
              data: [820, 932, 901, 934, 1290, 1330, 1320]
          }
        ]
      },
    }
  },
  beforeRouteUpdate (to, from, next) {
    if(!to.params.season) to.params.season = "정규시즌";
    //console.log('정말 행복 한거니', this.$route.params);
    let self = this;
    const name = to.params.name;
    const season = to.params.season;
    let url = '/api/player/pstat/' + name + '/' + season;
    fetch(url)
    .then(function(response) {
        return response.json();
    })
    .then(function(json) {
      self.$refs.tuiGrid.invoke("resetData", json.data.contents);
      next();
    });
  },
  updated () {    
    /*
    let self = this;
      fetch("../../static/goal_attempts.json")
      .then(function(response) {
        if (response.status !== 200) {
          console.log(
            "Looks like there was a problem. Status Code: " + response.status
          );
          return;
        }
        response.json().then(function(data) {
          console.log("hello?", data[1]);
          self.doesitwork = data;
          self.positionX = map(data, "position_x");
          self.positionY = map(data, "position_y");
        });
      })
      .catch(function(err) {
        console.log("Fetch Error :-S", err);
      });
    */
  },
  created () {
    console.log(this.$refs);
    if(!this.$route.params) this.$route.params.season = "정규시즌";
    //console.log('정말 행복 한거니', this.$route.params);
    let self = this;
    const name = this.name;
    const season = this.season;
    let url = '/api/player/pstat/' + name + '/' + season;
    self.gridProps = {
      data: {
        api: {
          readData: {
            url : () => url, method : 'GET',
          }
        },
        hideLoadingBar: false,
        //initialRequest: false,
      },
      options: {
        columnOptions: {
          frozenCount: 1,
          frozenBorderWidth: 1,
        },
      },
      columns: [
        { name: "season", header: "season", align: "center", sortable: true },
        //{ name: "No", header: "No", align: "center", sortable: true },
        //{ name: "NAME", header: "name", align: "left", sortable: true },
        { name: "start", header: "start", align: "center", sortable: true },
        //{ name: "pcode", header: "pcode", align: "center", sortable: true },
        { name: "G", header: "G", align: "center", sortable: true },
        { name: "MIN", header: "MIN", align: "right", sortable: true, width: 60 },
        { name: "TWOP", header: "2P", align: "right", sortable: true, },
        { name: "TWOPA", header: "2PA", align: "right", sortable: true, },
        { name: "TWOPPERCENT", header: "%", align: "right", sortable: true, },
        { name: "THREEP", header: "3P", align: "right", sortable: true, },
        { name: "THREEPA", header: "3PA", align: "right", sortable: true, },
        { name: "THREEPPERCENT", header: "%", align: "right", sortable: true, },
        { name: "FT", header: "FT", align: "right", sortable: true, },
        { name: "FTA", header: "FTA", align: "right", sortable: true, },
        { name: "FTPERCENT", header: "%", align: "right", sortable: true, },
        { name: "ORB", header: "ORB", align: "right", sortable: true, },
        { name: "DRB", header: "DRB", align: "right", sortable: true, },
        { name: "REB", header: "REB", align: "right", sortable: true, },
        { name: "AST", header: "AST", align: "right", sortable: true, },
        { name: "wFT", header: "wFT", align: "right", sortable: true, },
        { name: "woFT", header: "woFT", align: "right", sortable: true, },
        { name: "STL", header: "STL", align: "right", sortable: true, },
        { name: "BLK", header: "BLK", align: "right", sortable: true, },
        { name: "TOV", header: "TOV", align: "right", sortable: true, },
        { name: "DK", header: "DK", align: "right", sortable: true, },
        { name: "PTS", header: "PTS", align: "right", sortable: true, },
        { name: "PP", header: "PP", align: "right", sortable: true, },
        { name: "PF", header: "PF", align: "right", sortable: true, },
        { name: "TEAM", header: "TEAM", align: "center", sortable: true, width : 150},
      ]
    }
  },
  mounted () {
    console.log(this.$route.params);
  },
}

</script>